@import './_variables';

body {
	background-color: $black;
	padding: 0;
	margin: 0;
	//display: flex;
	//justify-content: center;
	color: #fff;
}

* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	&:before,
	&:after {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	&:focus {
		outline: none;
	}
}

*::-webkit-input-placeholder {
	color: #c0c0c0;
	opacity: 1;
}
*:-moz-placeholder {
	color: #c0c0c0;
	opacity: 1;
}
*::-moz-placeholder {
	color: #c0c0c0;
	opacity: 1;
}
*:-ms-input-placeholder {
	color: #c0c0c0;
	opacity: 1;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
	color: #000;
}

body input:required:valid,
body textarea:required:valid {
	color: #000;
}

html,
body {
	height: 100%;
}

body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 1.65;
	font-family: $default_font;
	overflow-x: hidden;
	opacity: 1;
}
img {
	max-width: 100%;
}

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
}

#root {
	height: 100%;
}

.select2-selection {
	&__control {
		cursor: pointer !important;
		margin-bottom: 2rem;
		border-radius: 10px !important;
		min-height: 48px !important;
		background-color: $purple !important;
		border: 1px solid transparent !important;
		color: $white;
		font-size: 16px;
		width: 100%;
		&--is-focused {
			border-color: transparent !important;
			box-shadow: 0 0 0 1px rgba($accent, 0.3) !important;
		}
	}

	&__value-container {
		& > div {
			//width: auto !important;
			overflow: hidden !important;
			white-space: nowrap !important;
		}
	}

	&__menu {
		background-color: $purple !important;
		border-radius: 10px !important;

		&-list {
			padding: 0 !important;
			border-radius: 10px !important;
		}

		&-notice {
			color: #946e86 !important;
		}
	}

	&__option {
		&--is-focused {
			background-color: darken($purple, 7%) !important;
		}
		&--is-selected {
			background-color: darken($purple, 8%) !important;
		}
	}

	&__indicator,
	&__indicator:hover {
		color: $white !important;
	}

	&__placeholder {
		color: #946e86 !important;
		margin-left: 6px !important;
	}

	&__single-value {
		color: $white !important;
	}

	&__multi-value {
		//width: min-content;
		border-radius: 6px !important;
		background-color: lighten($purple, 10%) !important;

		&__label {
			color: $white !important;
		}

		&__remove:hover {
			background-color: #8b2557 !important;
			color: $white !important;
		}
	}

	&__input {
		color: $white !important;
	}
}

.dark-link {
	margin: 0 auto;
	display: block;
	text-align: right;
	width: 100%;

	margin-bottom: 0.5rem;
	text-decoration: none;
	color: $purple;
	transition: color 0.3s ease;
	&:hover {
		color: darken($accent, 10%);
	}
}
.wrapper-inner-page {
	padding: 40px 30px;
	text-align: center;
	max-width: 560px;
	width: 100%;
	margin: 0 auto;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

input[type='number'] {
	-moz-appearance: textfield;
}
